class Home extends React.Component {
	state = {
	    showMAModal: false,
	    showWMModal: false,
	   	showCCModal: false,
	   	showCKModal: false
	  };
	  showMAModal = e => {
	    this.setState({
	      showMAModal: !this.state.showMAModal
	    });
	  };
	  showWMModal = e => {
	    this.setState({
	      showWMModal: !this.state.showWMModal
	    });
	  };
	  showCCModal = e => {
	    this.setState({
	      showCCModal: !this.state.showCCModal
	    });
	  };
	  showCKModal = e => {
	    this.setState({
	      showCKModal: !this.state.showCKModal,
	    });
	  };

	render () {
		
		return (
			<div>

				<div id="intro">
					<div id="backdrop"></div>
		        	<div className="project-text">
				    	<div className="padding-top-2em">
					        <h2 className="padding-top-1em">I'm Rebecca, a designer + technologist in NYC.</h2>
					        <p className="padding-top-1em">Focusing on digital products, my work is a mix of user research, interaction design and code. Graduating in May 2020 with an MFA in Design and Technology from Parsons.</p>
					    </div>
					</div>

				</div>

				<Tabs/>

				<Process/>

				<FooterDark/>

			</div>
		)
	}
}
export default Home;